// Import dependencies
import * as faceapi from '@vladmandic/face-api';
import React, { useRef, useState, useEffect } from "react";
import * as tf from "@tensorflow/tfjs";
import Webcam from "react-webcam";
import "./App.css";
import { nextFrame } from "@tensorflow/tfjs";
// 2. TODO - Import drawing utility here
import { drawRect } from "./utilities"; 


function App() {
  const webcamRef = useRef(null);
    const canvasRef = useRef(null);
    var dataURI;
    var intervId;
    var coord1;
    var coord2;
    var width;
    var height;
    var isDetected = false;
    var webcam = new Image();
    var imgData;
    var sdrogo;
    var gg = "gg";
    var videoW;
    var videoH;
    var i;
    var imm;
    var imma2 = "";
    const node = document.createElement("li");
    node.id = "lista immagini";
    node.style.display = "block";
    
  // Main function
    const runCoco = async () => {
        // 3. TODO - Load network 
        const net = await tf.loadGraphModel('https://testtfodausa.s3.eu-de.cloud-object-storage.appdomain.cloud/model.json');

        /* Promise.all([
            faceapi.nets.ssdMobilenetv1.loadFromUri("https://testtfodausa.s3.eu-de.cloud-object-storage.appdomain.cloud"),
            faceapi.nets.faceLandmark68Net.loadFromUri("https://testtfodausa.s3.eu-de.cloud-object-storage.appdomain.cloud"),
            faceapi.nets.faceRecognitionNet.loadFromUri("https://testtfodausa.s3.eu-de.cloud-object-storage.appdomain.cloud"),
        ]);
        */
        Promise.all([
            faceapi.nets.ssdMobilenetv1.loadFromUri(process.env.PUBLIC_URL + "/models"),
            faceapi.nets.faceLandmark68Net.loadFromUri(process.env.PUBLIC_URL + "/models"),
            faceapi.nets.faceRecognitionNet.loadFromUri(process.env.PUBLIC_URL + "/models"),
        ]);


        function getLabeledFaceDescriptions() {
            const labels = ["Elon Musk", "Marco", "Obama"];
            return Promise.all(
                labels.map(async (label) => {

                    var descriptions = [];

                    for (i = 1; i <= 8; i++) {
                        const image = await faceapi.fetchImage(process.env.PUBLIC_URL + `/labels/${label}/${i}.jpeg`);
                        //console.log(process.env.PUBLIC_URL + `/labels/${label}/${i}.jpeg`);
                        const detections = await faceapi
                            .detectSingleFace(image)
                            .withFaceLandmarks()
                            .withFaceDescriptor();

                        descriptions.push(detections.descriptor);
                    }
                    return new faceapi.LabeledFaceDescriptors(label, descriptions)
                })
            );
        }

        function faceRecognition(gg) {
            
            
            //console.log(results.values());
            //console.log("prima loop");
            const imma = new Image(640, 480);
            imma.src = gg.src;
            imma.onload = async function () {
                const labaledFaceDescriptors = await getLabeledFaceDescriptions();
                const faceMatcher = new faceapi.FaceMatcher(labaledFaceDescriptors);

                const canvas = faceapi.createCanvasFromMedia(imma);
                console.log(canvas);


                const displaySize = { width: 640, height: 480 };
                faceapi.matchDimensions(canvas, displaySize);


                const detections = await faceapi
                    .detectAllFaces(imma)
                    .withFaceLandmarks()
                    .withFaceDescriptors();

                console.log(detections);

                const resizedDetections = faceapi.resizeResults(detections, displaySize);
                canvas.getContext("2d").clearRect(0, 0, 640, 480);

                const results = resizedDetections.map((d) => {
                    return faceMatcher.findBestMatch(d.descriptor);
                });
                console.log(results);
                var t = 0;
                results.forEach((result, i) => {
                    const box = resizedDetections[i].detection.box;
                    const drawBox = new faceapi.draw.DrawBox(box, {
                        label: result,
                    });
                    console.log(t);
                    canvas.getContext("2d").clearRect(0, 0, 640, 480);
                    canvas.width = 640;
                    canvas.height = 480;
                    canvas.style.zIndex = 10;
                    canvas.id = "ritaglio";
                    const ctx = canvas.getContext("2d");
                    
                    if (imma2 !== gg.src) {
                    ctx.drawImage(imma, 0, 0);
                    drawBox.draw(canvas);
                    console.log(canvas);
                    document.body.append(node);
                    node.appendChild(canvas);
                    imma2 = gg.src;
                    t++;
                    }
                    console.log("test");
                    
                   
                    

                });
                
            }

        

            

        }

        // Loop and detect hands
        setInterval(() => {
            detect(net);
        }, 16.7);


        if (!intervId) {
            console.log(intervId);
            intervId = setInterval(() => {
                updateImageSource();
            }, 5000);
        }


        async function updateImageSource() {
            // TODO se l'oggetto detectato ï¿½ una persona all'ora si al ritaglio, altrimenti niente
            if (isDetected && sdrogo) {
                var c = document.createElement('canvas');
                c.width = 640;
                c.height = 480;
                var ct = c.getContext('2d');
                var imm = new Image(640, 480);
                imm.src = sdrogo;
                imm.onload = async function () {
                    ct.drawImage(imm, coord1 * 640, coord2 * 480, width * 640, height * 480, 0, 0, width * 640, height * 480);
                    dataURI = c.toDataURL('image/png');
                    document.getElementById("screenshot").src = dataURI;
                    isDetected = false;
                    const gg = document.getElementById("screenshot");
                    faceRecognition(gg);
                }
                
            }
        }
    };

  

  

  const detect = async (net) => {
    // Check data is available
     if (
       typeof webcamRef.current !== "undefined" &&
       webcamRef.current !== null &&
       webcamRef.current.video.readyState === 4
     ) {
       // Get Video Properties
       const video = webcamRef.current.video;
       const videoWidth = webcamRef.current.video.videoWidth;
       const videoHeight = webcamRef.current.video.videoHeight;

       // Set video width
       webcamRef.current.video.width = videoWidth;
       webcamRef.current.video.height = videoHeight;

       // Set canvas height and width
       canvasRef.current.width = videoWidth;
       canvasRef.current.height = videoHeight;

       // 4. TODO - Make Detections
       const img = tf.browser.fromPixels(video)
       const resized = tf.image.resizeBilinear(img, [640, 480])
       const casted = resized.cast('int32')
       const expanded = casted.expandDims(0)
       const obj = await net.executeAsync(expanded)

       

       const boxes = await obj[1].array()
       const classes = await obj[0].array()
       const scores = await obj[4].array()

       // Draw mesh
       const ctx = canvasRef.current.getContext("2d");
        

       // 5. TODO - Update drawing utility
       // drawSomething(obj, ctx) 
         if (scores[0][0] > 0.7) {
             //var webcam = new Image();
             sdrogo = await webcamRef.current.getScreenshot();
             videoW = videoWidth;
             isDetected = true;
             coord1 = boxes[0][0][1];
             coord2 = boxes[0][0][0];
             width = boxes[0][0][3] - boxes[0][0][1];
             height = boxes[0][0][2] - boxes[0][0][0];
         }
       requestAnimationFrame(()=>{drawRect(boxes[0], classes[0], scores[0], 0.70, videoWidth, videoHeight, ctx)}); 
         

         

         

       tf.dispose(img)
       tf.dispose(resized)
       tf.dispose(casted)
       tf.dispose(expanded)
       tf.dispose(obj)

      }
      return sdrogo;
  };

  useEffect(() => {runCoco()},[]);

  return (
    <div className="App">
      <header className="App-header">
        <Webcam
          ref={webcamRef}
          id="webcam"
          muted={true} 
          style={{
            position: "absolute",
            marginLeft: "auto",
            marginRight: "auto",
            left: 0,
            right: 0,
            textAlign: "center",
            zindex: 9,
            width: 640,
            height: 480,
          }}
              />

        <canvas
          ref={canvasRef}
          style={{
            position: "absolute",
            marginLeft: "auto",
            marginRight: "auto",
            left: 0,
            right: 0,
            textAlign: "center",
            zindex: 8,
            width: 640,
            height: 480,
          }}
        />

              
                  <img
                      
                      id="screenshot"
                      alt="Screenshot"
                      style={{
                          width: 640,
                          height: 480,
                      }}
                      />
      </header>
    </div>
  );
}

export default App;
